@import "src/sass/variables";

.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
    background-color: $yellowColor;
    border-radius: 4px;
}

.dialogsItems {
    padding: 10px;
    color: white;
}

.dialogsItems .active {
    color: gold;
}

.messages {
    padding: 10px;
}
.messages .message {
    color: white;
}